import React from "react";
import Loader from "../loader/loader";
import "./item-list.css";

export default class ItemList extends React.Component {
  state = {
    AllData: null,
    loader: true
  };

  componentDidMount() {
    this.props.send().then(value => {
      this.setState(() => {
        let data = value.results;
        data.map(e => {
          return (e.url = e.url.match(/[\d]/gm).join(""));
        });
        return {
          AllData: data,
          loader: false
        };
      });
    });
  }

  renderPeople = value => {
    return value.map(e => {
      let data = this.props.instance(e);
      return (
        <li key={e.name} onClick={({ url }) => this.props.getId(e.url)}>
          {data}
        </li>
      );
    });
  };

  render() {
    let information = this.state.loader ? (
      <Loader />
    ) : (
      this.renderPeople(this.state.AllData)
    );

    return (
      <div>
        <ul className="item-list">{information}</ul>
      </div>
    );
  }
}
