import React from "react";
import Loader from "../loader/loader";
import "./Details.css";

export default class DetailSpecies extends React.Component {
  state = {
    detailData: null,
    loader: false
  };

  componentDidMount() {
    if (this.props.SpeciesId === null) {
      return;
    }
    this.setState({
      loader: true
    });
    this.getDetailes();
  }
  componentDidUpdate(prevProps) {
    if (prevProps.SpeciesId !== this.props.SpeciesId) {
      this.setState({
        loader: true
      });
      this.getDetailes();
    }
  }

  getDetailes() {
    this.props.description(this.props.SpeciesId).then(e => {
      this.setState({
        detailData: e,
        loader: false
      });
    });
  }

  render() {
    let info = { ...this.state.detailData };

    if (this.state.loader) {
      return <Loader />;
    } else {
      return <InfoBody info={info} id={this.props.SpeciesId} />;
    }
  }
}

class InfoBody extends React.Component {
  render() {
    if (this.props.id === null) {
      return (
        <div className="information">
          <h3 className="choice">Choose species in left side</h3>
        </div>
      );
    } else {
      return (
        <div className="information">
          <p>
            <span>Species:</span>
            {this.props.info.name}
          </p>
          <p>
            <span>classification: </span>
            {this.props.info.classification}
          </p>
          <p>
            <span>average_height: </span>
            {this.props.info.average_height}
          </p>
          <p>
            <span>average_lifespan: </span>
            {this.props.info.average_lifespan}
          </p>
          <img
            src={`https://starwars-visualguide.com/assets/img/species/${
              this.props.id
            }.jpg`}
            alt="species"
          />
        </div>
      );
    }
  }
}
