import React from "react";
import Header from "../header/header";
import RandomPlanet from "../random-planet/random-planet";
import ItemList from "../item-list/item-list";
import DetailPeople from "../DetailInfo/DetailPeople";
import DetailStarShips from "../DetailInfo/DetailStarShips";
import DetailSpecies from "../DetailInfo/DetailSpecies";
import Requests from "../requests-api/requests-api";
import "./style.css";
import { BrowserRouter as Router, Route } from "react-router-dom";

export default class Main extends React.Component {
  state = {
    PeopleId: null,
    StarShipsId: null,
    SpeciesId: null
  };

  sendRequest = new Requests();

  render() {
    let getIdPerson = e => {
      this.setState({
        PeopleId: e
      });
    };

    let getIdStarShip = e => {
      this.setState({
        StarShipsId: e
      });
    };

    let getIdSpecies = e => {
      this.setState({
        SpeciesId: e
      });
    };
    return (
      <Router>
        <div>
          <Header />
          <Route path="/" exact component={RandomPlanet} />
          <Route
            path="/people"
            exact
            render={() => (
              <div>
                <h2 className="page-title">People</h2>
                <div className="wrapper-description">
                  <ItemList
                    send={this.sendRequest.getPeople}
                    instance={event => event.name}
                    getId={getIdPerson}
                  />
                  <DetailPeople
                    description={this.sendRequest.getPeople}
                    personId={this.state.PeopleId}
                  />
                </div>
              </div>
            )}
          />
          <Route
            path="/people/:id"
            render={match => {
              let id = match.match.params.id;
              return (
                <DetailPeople
                  description={this.sendRequest.getPeople}
                  personId={id}
                />
              );
            }}
          />
          <Route
            path="/starships"
            exact
            render={() => (
              <div>
                <h2 className="page-title">Starships</h2>
                <div className="wrapper-description">
                  <ItemList
                    send={this.sendRequest.getStarships}
                    instance={event => event.name}
                    getId={getIdStarShip}
                  />
                  <DetailStarShips
                    description={this.sendRequest.getStarships}
                    StarShipId={this.state.StarShipsId}
                  />
                </div>
              </div>
            )}
          />
          <Route
            path="/starships/:id"
            render={match => {
              let id = match.match.params.id;
              return (
                <DetailStarShips
                  description={this.sendRequest.getStarships}
                  StarShipId={id}
                />
              );
            }}
          />
          <Route
            path="/species"
            render={() => (
              <div>
                <h2 className="page-title">Species</h2>
                <div className="wrapper-description">
                  <ItemList
                    send={this.sendRequest.getSpecies}
                    instance={event => event.name}
                    getId={getIdSpecies}
                  />
                  <DetailSpecies
                    description={this.sendRequest.getSpecies}
                    SpeciesId={this.state.SpeciesId}
                  />
                </div>
              </div>
            )}
          />
          <Route
            path="/species/:id"
            render={match => {
              let id = match.match.params.id;
              return (
                <DetailSpecies
                  description={this.sendRequest.getSpecies}
                  SpeciesId={id}
                />
              );
            }}
          />
        </div>
      </Router>
    );
  }
}
