export default class Requests {
  async getData(url) {
    let result = await fetch(url);
    result = result.json();
    return await result;
  }

  getPlanets = async (id = "") => {
    let a = await this.getData(`https://swapi.co/api/planets/${id}`);
    return a;
  };

  getPeople = async (id = "") => {
    let a = await this.getData(`https://swapi.co/api/people/${id}`);
    return a;
  };

  getSpecies = async (id = "") => {
    let a = await this.getData(`https://swapi.co/api/species/${id}`);
    return a;
  };
  getStarships = async (id = "") => {
    let a = await this.getData(`https://swapi.co/api/starships/${id}`);
    return a;
  };
}
