import React from "react";
import Loader from "../loader/loader";
import "./Details.css";

export default class DetailPeople extends React.Component {
  state = {
    detailData: null,
    loader: null
  };

  componentDidMount() {
    if (this.props.personId === null) {
      return;
    }
    this.setState({
      loader: true
    });
    this.getDetailes();
  }
  componentDidUpdate(prevProps) {
    if (prevProps.personId !== this.props.personId) {
      this.setState({
        loader: true
      });
      this.getDetailes();
    }
  }

  getDetailes() {
    this.props.description(this.props.personId).then(e => {
      this.setState({
        detailData: e,
        loader: false
      });
    });
  }

  render() {
    let info = { ...this.state.detailData };

    if (this.state.loader) {
      return <Loader />;
    } else {
      return <InfoBody info={info} id={this.props.personId} />;
    }
  }
}

class InfoBody extends React.Component {
  render() {
    if (this.props.id === null) {
      return (
        <div className="information">
          <h3 className="choice">Choose character in left side</h3>
        </div>
      );
    } else {
      return (
        <div className="information">
          <p>
            <span>name: </span>
            {this.props.info.name}
          </p>
          <p>
            <span>height: </span>
            {this.props.info.height}{" "}
          </p>
          <p>
            <span>mass:</span> {this.props.info.mass}
          </p>
          <p>
            <span>gender: </span>
            {this.props.info.gender}
          </p>
          <img
            src={`https://starwars-visualguide.com/assets/img/characters/${
              this.props.id
            }.jpg`}
            alt=""
          />
        </div>
      );
    }
  }
}
