import React from "react";
import Requests from "../requests-api/requests-api";
import Loader from "../loader/loader";
import "./random-planet.css";
const Planets = new Requests();

export default class RandomPlanet extends React.Component {
  state = {
    name: null,
    id: null,
    diameter: null,
    rotation_period: null,
    population: null,
    loader: true
  };

  componentDidMount() {
    this.updatePlanet();
    this.intervalId = setInterval(this.updatePlanet, 5000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  updatePlanet = () => {
    let id = Math.floor(Math.random() * 15 + 3);
    Planets.getPlanets(id).then(value => {
      this.setState({
        id: id,
        name: value.name,
        diameter: value.diameter,
        rotation_period: value.rotation_period,
        population: value.population,
        loader: false
      });
    });
  };

  render() {
    let dowloading = this.state.loader ? <Loader /> : null;
    let body = !this.state.loader ? (
      <PlanetView updatePlanet={this.updatePlanet} info={this.state} />
    ) : null;
    return (
      <div className="planet">
        {dowloading}
        {body}
      </div>
    );
  }
}

class PlanetView extends React.Component {
  render() {
    let data = this.props.info;
    return (
      <div>
        <h2>
          <span>Name: </span>
          {data.name}
        </h2>
        <img
          src={`https://starwars-visualguide.com/assets/img/planets/${
            data.id
          }.jpg`}
          alt=""
        />
        <p>
          <span>Population: </span>
          {data.population}
        </p>
        <p>
          <span>Diameter: </span>
          {data.diameter}
        </p>
        <p>
          <span>Period: </span>
          {data.rotation_period}{" "}
        </p>
      </div>
    );
  }
}
