import React from "react";
import Loader from "../loader/loader";
import "./Details.css";

export default class DetailStarShips extends React.Component {
  state = {
    detailData: null,
    loader: false
  };

  componentDidMount() {
    if (this.props.StarShipId === null) {
      return;
    }
    this.setState({
      loader: true
    });
    this.getDetailes();
  }
  componentDidUpdate(prevProps) {
    if (prevProps.StarShipId !== this.props.StarShipId) {
      this.setState({
        loader: true
      });
      this.getDetailes();
    }
  }

  getDetailes() {
    this.props.description(this.props.StarShipId).then(e => {
      this.setState({
        detailData: e,
        loader: false
      });
    });
  }

  render() {
    let info = { ...this.state.detailData };

    if (this.state.loader) {
      return <Loader />;
    } else {
      return <InfoBody info={info} id={this.props.StarShipId} />;
    }
  }
}

class InfoBody extends React.Component {
  render() {
    if (this.props.id === null) {
      return (
        <div className="information">
          <h3 className="choice">Choose starship in left side</h3>
        </div>
      );
    } else {
      return (
        <div className="information">
          <p>
            <span>name: </span>
            {this.props.info.name}
          </p>
          <p>
            <span>model: </span>
            {this.props.info.model}
          </p>
          <p>
            <span>manufacturer: </span>
            {this.props.info.manufacturer}
          </p>
          <p>
            <span>max_atmosphering_speed: </span>
            {this.props.info.max_atmosphering_speed}
          </p>
          <img
            src={`https://starwars-visualguide.com/assets/img/starships/${
              this.props.id
            }.jpg`}
            alt=""
          />
        </div>
      );
    }
  }
}
